body {  
  height: 100vh;
  margin: 20px;

  font: 14px "Century Gothic", Futura, sans-serif;
    
  background: linear-gradient(to bottom, #fff, #dfd);
}

ol, ul {
  padding-left: 30px;
}

#root {
  display: flex;
  justify-content: center;
}

.btn {
  margin-bottom: 5px;
  padding: 5px;
  font: 14px "Century Gothic", Futura, sans-serif;
  line-height: 14px;

  background-color:#fff;
  border: 1px solid #555;
  border-radius: 5px
}

.board-row {
  display: flex;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  width: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;

  font-size: 24px;
  font-weight: bold;
  line-height: 34px;    
  text-align: center;

  background: #fff;
  border: 1px solid #999;
}

.square-winner {
  background-color: #afa;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;

  background-color: #fff;
  border: 1px solid #000;
  box-shadow: 0 1px 1px rgba(0,0,0,0.15),
              /* The second layer */
              0 10px 0 -5px #eee,
              /* The second layer shadow */
              0 10px 1px -4px rgba(0,0,0,0.15),
              /* The third layer */
              0 20px 0 -10px #eee,
              /* The third layer shadow */
              0 20px 1px -9px rgba(0,0,0,0.15),
              /* Blur shadow */
              10px 10px 30px rgba(0, 0, 0, .2);

}

.game-board {
  margin-bottom: 20px;
}

.game-info {
  margin-left: 20px;
}

.status {
  padding: 20px;
  border: 1px solid #fff;
  border-radius: 15px;
}

.highlight {
  background-color: #afa;
  border: 1px solid #5f5;
  border-radius: 15px;
}

